  padding-top: 8px;
  border-top-style: none;
  text-align: initial;
}

.ptdrender-resize {
  display: inline-table;
}

.label-font-size {
  font-size: 12px;
}

ptable > div.md-table-container table.md-table {
  border-collapse: unset;
}

.th-span-format {
  white-space: normal;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
}

.th-span-fielset {
  display: table-cell;
  vertical-align: middle;
}

.no-border-bottom {
  border-bottom-style: none;
}

.th-ptcheckbox {
  width: 65px !important;
}

.th-more-info {
  width: 70px !important;
}

